}

#titolCap {
  color: #fff;
  text-transform: uppercase;
  padding-left: 20px;
  animation-name: animacioTitolCap;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

#imatgeCap {
  width: 100px;
}

#barraMenu {
  color: #fff;
  justify-content: center;
