}

#menu {
  flex-direction: row;
  padding: 10px;
  list-style: none;
  display: flex;
}

.opcio {
  padding-left: 10px;
  padding-right: 10px;
}

a {
  color: #fff;
  -webkit-text-decoration-line: none;
  text-decoration-line: none;
}

