  color: #888;
}

.opcioActiva {
  color: #0ff;
}

main {
  margin-top: 80px;
  margin-bottom: 100px;
}

#presentacio {
  opacity: .8;
  background-color: #555;
  border-style: solid;
  border-color: gray;
  border-radius: 5px;
  margin-top: 20px;
}

#grups {
  flex-flow: wrap;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
}

.grup {
  width: 150px;
  height: 250px;
  margin-left: 10px;
  margin-right: 10px;
  display: block;
}

.grup:hover {
  animation: 1s animacioGrups;
}

.imgGrup {
  object-fit: cover;
  width: 150px;
  height: 150px;
  -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 50% 100%);
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 50% 100%);
  border-radius: 50%;
}

.grup h1 {
  color: #fff;
  text-align: center;
  font-size: 18pt;
}

.swiper {
  height: 50vh;
  flex-direction: column;
  align-content: center;
  align-items: center;
  display: flex;
}

.swiper-wrapper {
