  font-size: 42px;
}

#containerCat {
  margin: auto;
  padding-top: 80px;
}

.llista {
  flex-flow: wrap;
  align-content: center;
  display: flex;
}

.punt {
  width: 160px;
  text-align: center;
  background-color: #555;
  border-radius: 5px;
  margin: 10px;
  padding: 5px;
  list-style-type: none;
  display: block;
}

.puntActiu {
  width: 160px;
  text-align: center;
  background-color: #faebd7;
  border-radius: 5px;
  margin: 10px;
  padding: 5px;
  list-style-type: none;
  display: block;
}

.puntTitol {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  font-size: .8em;
  font-weight: 800;
}

.miniatura {
  height: 100px;
  width: 150px;
  border-radius: 5px;
  box-shadow: 0 8px 8px #00000026;
}

.descripcio {
  text-align: left;
  margin-bottom: 0;
  padding-bottom: 0;
  font-size: 14pt;
  font-weight: 800;
}

.categoria {
  text-align: center;
  margin-top: 0;
  padding-top: 0;
  -webkit-text-decoration-style: double;
  text-decoration-style: double;
}

footer {
  color: #fff;
  min-width: 100%;
  background-color: #333;
  background-image: url("fonsCap.0e688d77.jpg");
  flex-direction: row;
  justify-content: space-around;
  display: flex;
  position: absolute;
  bottom: 0;
  box-shadow: 0 -8px 8px #00000026;
}

#imatgePeu {
  width: 100px;
}

#imatgePeu2 {
  width: 150px;
}

.svgImg {
  animation-name: animacioSVG;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

footer div {
  text-align: center;
  flex: auto;
  padding-right: 20px;
}

.llistaPeu {
  list-style-type: none;
}

.despPeu {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  width: 200px;
  background-color: #000;
  border: none;
  font-family: Montserrat, sans-serif;
  font-size: 12pt;
}

@media screen and (max-width: 750px) {
  header {
    height: 150px;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
  }

  #imatgeCap {
    padding-left: 20px;
    padding-right: 20px;
  }

  #titolCap {
    color: #fff;
    text-transform: uppercase;
    padding: 0;
  }

  #menu {
    flex-direction: row;
    margin: 0;
    padding: 0;
  }

  main {
    margin-top: 150px;
    margin-bottom: 350px;
  }

  .swiper {
    height: 50vh;
  }

  .swiper-button-next, .swiper-button-prev {
    visibility: hidden;
    outline: none;
  }

  div.swiper-button-next, div.swiper-button-prev {
    top: 50%;
  }

  .swiper-slide {
    color: #000;
  }

  .swiper-slide img {
    width: 100%;
  }

  .contingut {
    text-align: center;
    width: 100%;
    padding: 0;
    font-size: 14px;
  }

  .swiper-wrapper h1 {
    text-align: center;
    text-shadow: 1px 1px 2px #000;
    font-size: 3rem;
  }

  footer {
    flex-direction: column;
  }
}

